    width: 30px;
    height: 30px;
  }
}

/* Animation Keyframes */
@-webkit-keyframes ball1 {
  0% {
    top: 10px;
  }
  50% {
    top: 100px;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    top: 10px;
  }
}
@keyframes ball1 {
  0% {
    top: 10px;
  }
  50% {
    top: 100px;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    top: 10px;
  }
}

@-webkit-keyframes ball2 {
  0% {
    top: 100px;
  }
  50% {
    top: 10px;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  100% {
    top: 100px;
  }
}

@keyframes ball2 {
  0% {
    top: 100px;
  }
  50% {
    top: 10px;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  100% {
    top: 100px;
  }
}

@-webkit-keyframes mymove {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.1;
  }
  20% {
    opacity: 0.2;
  }
  30% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.5;
  }
  60% {
    opacity: 0.6;
  }
  70% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@keyframes mymove {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.1;
  }
  20% {
    opacity: 0.2;
  }
  30% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.5;
  }
  60% {
    opacity: 0.6;
  }
  70% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

